.button{width:120px;text-align:center;border:none;border-radius:3px;background-color:#355c7d;padding:1em;outline:none;cursor:pointer;color:#edf1f4;text-transform:uppercase;transition:.3s;font-size:10px}@media screen and (min-width:480px){.button{font-size:11px;width:150px}}@media screen and (min-width:768px){.button{font-size:14px}}.button:hover{opacity:.9}